import { Suspense, useEffect, useState, useRef } from 'react';
import { Project } from '@inweso-dev/i18n-store';
import * as i18nextV4WithContext from '@inweso-dev/format-i18next-v4-withcontext';
import * as i18nextV3WithContext from '@inweso-dev/format-i18next-v3-withcontext';
import { Hydration } from '@inweso-dev/hydration-browser';
import flatten from 'flat';
import Modal from 'react-modal';
import defaultProject from './defaultProject.json';
import Loader from 'react-loader-spinner';

import {
  SegmentListFilter,
  ProjectData,
  Layout,
  LeftRow,
  MiddleRow,
  SegmentListHeader,
  SegmentAdd,
  SegmentCopy,
  SegmentList,
  add,
} from '@inweso-dev/cat-ui';

import * as fileJSON from '@inweso-dev/file-json';
import * as fileYAML from '@inweso-dev/file-yaml';
// import * as fileLocize from '@inweso-dev/file-locize';
import * as fileTMX from '@inweso-dev/file-tmx';
import * as fileAndroid from '@inweso-dev/file-android';
import * as fileCSV from '@inweso-dev/file-csv';
import * as fileGettext from '@inweso-dev/file-gettext';
import * as fileFluent from '@inweso-dev/file-fluent';
import * as fileLaravel from '@inweso-dev/file-laravel';
import * as fileProperties from '@inweso-dev/file-properties';
import * as fileRESX from '@inweso-dev/file-resx';
import * as fileStrings from '@inweso-dev/file-strings';
import * as fileXLIFF from '@inweso-dev/file-xliff';
import * as fileXSLX from '@inweso-dev/file-xlsx';

import '@inweso-dev/cat-ui/dist/cat-ui.css';
import './i18next.css';
import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';

// add file support
add(fileJSON);
add(fileYAML);
add(fileTMX);
add(fileAndroid);
add(fileCSV);
add(fileGettext);
add(fileFluent);
add(fileLaravel);
add(fileProperties);
add(fileRESX);
add(fileStrings);
add(fileXLIFF);
add(fileXSLX);

// Make sure to bind modal to your appElement (https://reactcommunity.org/react-modal/accessibility/)
Modal.setAppElement('#root');

function LoadingSpinner() {
  return <Loader className="loader" type="Rings" color="#26a69a" height={320} width={320} />
}

function App() {
  const [modalIsOpen, setIsOpen] = useState(false)
  const [projectData, setProjectData] = useState({});
  const opt = useRef();

  useEffect(() => {
    window.addEventListener('message', (event) => {
      if (!event.data || !event.data.message) return;
      if (event.data.message === 'isEditorReady') {
        event.source.postMessage({ message: 'editorIsReady' }, event.origin);
        return;
      }
      if (event.data.message === 'initializeProject') {
        opt.current = {
          sourceLng: event.data.sourceLng,
          targetLngs: event.data.targetLngs || Object.keys(event.data.resources).filter((l) => l !== event.data.sourceLng),
          preserveEmptyStrings: event.data.preserveEmptyStrings,
          i18nFormat: event.data.i18nFormat
        };
        setProjectData({
          resources: event.data.resources,
          ...opt.current
        })
      }
    }, false);
  }, []);
  
  useEffect(() => {
    setTimeout(() => {
      if (!opt.current) setProjectData(defaultProject);
    }, 2000)
  }, []);

  if (!projectData || !projectData.resources) return <LoadingSpinner />;

  const project = new Project('some-id', 'some-name', {
    ...projectData,
    i18n: (projectData.i18nFormat && projectData.i18nFormat.indexOf('i18next_v3') > -1) ? i18nextV3WithContext : i18nextV4WithContext,
    disableDateFilter: true,
    disableMissingFilter: true,
    readOnly: true,
    hydration: new Hydration(),
    actions: {
      save: async (diff) => {
        setIsOpen(true);
        diff.updated = diff.update;
        diff.deleted = diff.delete;
        return diff;
      },
      saveSegment: async (diff) => {
        setIsOpen(true);
        diff.updated = diff.update;
        diff.deleted = diff.delete;
        return diff;
      },
      handleImport: async () => setIsOpen(true),
      handleExport: async () => setIsOpen(true)
    },
  });

  Object.keys(projectData.resources).forEach((l) => {
    Object.keys(projectData.resources[l]).forEach((n) => {
      const res = flatten(projectData.resources[l][n]);
      const data = Object.keys(res).reduce((prev, key) => {
        prev[key] = { value: res[key] };
        return prev;
      }, {})
      project.import(l, n, data, { commit: true });
    });
  });

  return (
    <div className="flex-row">
      <div className="flex-outer">
        <div className="flex-inner">
          <h1>This GUI allows only a minimal functionality to get an overview of the translations.</h1>
          <p>To use the full feature set, use <a href="https://locize.com">locize</a>.</p>
        </div>
      </div>
      <div className="flex-rest">
        <Suspense fallback={<LoadingSpinner />}>
          <ProjectData project={project}>
            <Layout>
              <LeftRow>
                <SegmentListFilter />
              </LeftRow>
              <MiddleRow>
                <SegmentListHeader />
                <SegmentAdd />
                <SegmentCopy />
                <SegmentList />
              </MiddleRow>
            </Layout>
          </ProjectData>
          <Modal
            isOpen={modalIsOpen}
            contentLabel="Try locize!"
            onRequestClose={() => setIsOpen(false)}
            style={{
              content: {
                top: '50%',
                left: '50%',
                right: 'auto',
                bottom: 'auto',
                marginRight: '-50%',
                transform: 'translate(-50%, -50%)',
              }
            }}
          >
            <h2>Function not available here!</h2>
            <p>To use the full feature set, use <a href="https://locize.com">locize</a>.</p>
          </Modal>
        </Suspense>
      </div>
    </div>
  );
}

export default App;
