// circular dependencies in readable-stream
// rollup -> alias 'readable-stream': 'rollup-plugin-node-polyfills/polyfills/stream'
// plus npm postinstall hack into npm module replace shizzle!!
export async function parse(text) {
  let asr2js = await import('android-string-resource/esm/asr2js');
  if (asr2js.default) asr2js = asr2js.default;

  const data = await asr2js(text, { comment: 'right' });

  const ret = Object.keys(data).reduce((mem, k) => {
    let item = data[k];
    if (item !== 'string' && typeof item.comment === 'string') {
      item = {
        value: item.value,
        context: {
          text: item.comment,
        },
      };
    } else {
      item = { value: item.value || item };
    }

    mem[k] = item;
    return mem;
  }, {});

  return ret;
}

export async function toString(list) {
  let js2asr = await import('android-string-resource/esm/js2asr');
  if (js2asr.default) js2asr = js2asr.default;

  const dataJs = list.reduce((mem, item) => {
    mem[item.key] = item.data.value || '';
    if (item && item.data.context && item.data.context.text) {
      mem[item.key] = {
        value: mem[item.key],
        comment: item.data.context.text,
      };
    }

    return mem;
  }, {});

  const asrFile = await js2asr(dataJs);
  return asrFile;
}

export const extensions = ['xml'];
export function maybeFormat(str, ext) {
  let ret = 0;
  if (ext && extensions.indexOf(ext) > -1) ret = ret + 0.5;
  if (str.indexOf('</resources>') > -1) ret = ret + 0.5;
  return ret;
}

const extension = 'xml';
const color = '#a22b2b';
export const imp = [
  { name: 'android-string', api: 'single', type: 'dev', parse, maybeFormat, extension, color },
];
export const exp = [
  { name: 'android-string', api: 'single', type: 'dev', toString, extension, color },
];
